<template>
  <form class="row" @submit.prevent="submit()">
    <div class="col-lg-6">
        <div class="card">
            
            <div class="card-body">
                <div class="row">
                    <div class="col-md-12">
                        <h3>Cadastro de Grupo de Usuário</h3>
                    </div>
                </div>
                <div class="row">
                    <label class="col-sm-3 col-form-label">Id</label>
                    <div class="col-sm-9">
                        <input-componente
                            :disabled="true"
                            v-model="form.id"                            
                            />
                    </div>
                    
                    <label class="col-sm-3 col-form-label">Nome</label>
                    <div class="col-sm-9">
                        <input-componente
                            v-model="form.name"
                            :validator="$v.form.name"
                            @blur="$v.form.name.$touch"
                            />
                    </div>
                    <label class="col-sm-3 col-form-label">Guarda</label>
                    <div class="col-sm-9">
                        <input-componente
                            v-model="form.guard_name"
                            placeholder="web / api"
                            :validator="$v.form.guard_name"
                            @blur="$v.form.guard_name.$touch"
                            />
                    </div>
                    <label class="col-sm-3 col-form-label">Descrição</label>
                    <div class="col-sm-9">
                        <input-componente
                            v-model="form.description"
                            :validator="$v.form.description"
                            @blur="$v.form.description.$touch"
                            />
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12 text-center" v-if="erros || sucesso">
                        <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                        <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                    </div>
                    <div class="col-md-12 button-items text-center">

                        <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'gruposLista'}">
                            <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                        </router-link>

                        <button type="submit" class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                            <i class="bx bxs-save d-block font-size-16"></i> Salvar
                        </button>

                    </div>
                </div>
                <div class='row'>
                    <div class="col-md-12 text-right" v-if="form.id">
                        <log-componente :index="form.id" subject_type="App\Models\Spatie\Grupo"  :subject_id="form.id" ></log-componente>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="col-lg-6">
        <div class="card">
            
            <div class="card-body">
                <div class="row">
                    <div class="col-md-12">
                        <div class="page-title-box d-flex align-items-center justify-content-between">
                            <h4 class="mb-0 font-size-18">Permissões vinculadas</h4>
                            <div class="page-title-right">
                                <button type="button" data-toggle="modal" :disabled="!form.id" data-target="#modal-adicionar" data-backdrop="static" @click="abrirModalVincular" class="btn btn-outline-primary waves-effect waves-light">
                                    <i class="bx bxs-plus-square font-size-16 align-middle mr-2"></i> Adicionar
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-md-12">
                        <div class="table-responsive">
                                <table class="table mb-0" >
                                <thead>
                                    <tr>
                                        <th>Name</th>
                                        <th>Descrição</th>
                                        <th>#</th>
                                    </tr>
                                </thead>
                                <tbody>
                                <tr v-for="permissao in permissoes" :key="permissao.id">
                                    <td>{{permissao.name}} </td>
                                    <td>{{permissao.description}}</td>
                                    <td>
                                        <button type="button" class="btn btn-outline-danger waves-effect waves-light" style="padding: 3px 12px 3px 12px;" title="Desvincular"
                                                data-toggle="modal" data-target="#modal-delete" data-backdrop="static" @click="abrirModalDesvincular(permissao)">
                                            <i class="mdi mdi-trash-can d-block font-size-12"></i>
                                        </button>
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
              
            </div>
        </div>
    </div>

    <div class="modal fade" id="modal-delete" tabindex="-1" aria-labelledby="modal-delete-title" aria-hidden="true" style="display: none;">
        <div class="modal-dialog modal-dialog-scrollable">
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title mt-0" id="modal-deleteTitle">  Confirmação de Exclusão de Registro</h4>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <div class="modal-body" v-if="permissaoModal">
                    <p v-if="!sucessoPermissoes">Tem certeza que deseja descinvular a permissão <strong>{{permissaoModal.name}}</strong>  deste grupo de usuários?</p>
                    
                    <div v-if="sucessoPermissoes" class="text-center" >
                        <div class="swal2-icon swal2-success swal2-icon-show" style="display: flex;">
                            <div class="swal2-success-circular-line-left" style="background-color: rgb(255, 255, 255);"></div>
                            <span class="swal2-success-line-tip"></span> <span class="swal2-success-line-long"></span>
                            <div class="swal2-success-ring"></div> <div class="swal2-success-fix" style="background-color: rgb(255, 255, 255);"></div>
                            <div class="swal2-success-circular-line-right" style="background-color: rgb(255, 255, 255);"></div>
                        </div>
                        <p class="success text-center font-size-18">{{sucessoPermissoes}}</p>
                        
                    </div>
                    <mensagem-componente titulo="Erro!" v-if="errosPermissoes" :erros="errosPermissoes" @fechar="errosPermissoes = null"></mensagem-componente>
                    
                </div>
                <div v-else class="modal-body"></div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-danger" :disabled="sucessoPermissoes || !permissaoModal" @click.prevent="desvincularPermissao()"><i class="mdi mdi-trash-can font-size-18"></i> Sim </button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-thick font-size-18"></i> Voltar</button>                        
                </div>
            </div><!-- /.modal-content -->
        </div><!-- /.modal-dialog -->
    </div>

     <div class="modal fade" id="modal-adicionar" tabindex="-1" aria-labelledby="modal-adicionar-title" aria-hidden="true" style="display: none;">
        <div class="modal-dialog modal-dialog-scrollable">
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title mt-0" id="modal-adicionarTitle">  Selecione uma nova permissão</h4>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">×</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-md-12">
                            <div class="form-group">
                                <label class="control-label">Permissão</label>
                                <vue-select style="height: calc(1.5em + 0.94rem - 6px)"  placeholder="Comece a digitar para buscar"
                                    :options="permissoesBusca"
                                    @search="buscarPermissoesDebounce"
                                    :disabled="sucessoPermissoes"
                                    v-model="permissaoModal" label="name">
                                    <template slot="option" slot-scope="option">
                                        {{ option.name }} | {{ option.guard_name}}
                                    </template>
                                    <div slot="no-options">Nenhum item encontrado!</div>
                                    <div class="spinner" v-show="buscandoPermissao">Buscando...</div>
                                </vue-select>
                            </div>
                        </div>
                        <div class="col-md-12" v-if="permissaoModal && permissaoModal.id">
                            <div class="form-group">
                                <label class="control-label">Guarda</label>
                                <input type="text" :disabled="true" class="form-control" :value="permissaoModal.guard_name">
                            </div>
                        </div>
                        <div class="col-md-12" v-if="permissaoModal && permissaoModal.id">
                            <div class="form-group">
                                <label class="control-label">Descrição</label>
                                <input type="text" :disabled="true" class="form-control" :value="permissaoModal.description">
                            </div>
                        </div>
                        <div v-else class="col-md-12" style="height: 100px">
                            
                        </div>
                    </div>
                    <div class="row" v-if="errosPermissoes || sucessoPermissoes">
                        <div class="col-md-12 text-center">
                            <mensagem-componente v-if="errosPermissoes" :erros="errosPermissoes" @fechar="errosPermissoes = null"></mensagem-componente>
                            <mensagem-componente v-if="sucessoPermissoes" :sucesso="sucessoPermissoes" @fechar="sucessoPermissoes = null"></mensagem-componente>
                        </div>
                    </div>
                
                </div>
                <div class="modal-footer">
                    
                    <button type="button" class="btn btn-success" v-if="sucessoPermissoes" @click.prevent="abrirModalVincular()"><i class="bx bxs-plus-square font-size-18"></i> Vincular Outra </button>
                    <button type="button" class="btn btn-primary" :disabled="sucessoPermissoes || !permissaoModal" @click.prevent="vincularPermissao()"><i class="bx bxs-save font-size-18"></i> Vincular </button>
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="bx bx-window-close font-size-18"></i> Voltar</button>                        
                </div>
            </div><!-- /.modal-content -->
        </div><!-- /.modal-dialog -->
    </div>
</form>
</template>

<script>

import { required, maxLength } from "vuelidate/lib/validators";
import { UserService } from '../../../services/UserService'
const userService = new UserService();

export default {

  data() {
        return {
            form: {
                id: null,
                name: '',
                guard_name: '',
                description: '',
            },
            sucesso: null,
            erros:null,
            errosTitulo:'',
            errosPermissoes: null,
            sucessoPermissoes: null,

            permissoes: [],
            permissaoModal: null,
            permissoesBusca: [],
            timer: null,
            buscandoPermissao: true
        } ;
    },
    validations: {
        form: {
            name: {
                required, maxLength: maxLength(50)
            },
            guard_name: {
                required, maxLength: maxLength(50) 
            },
            description: {
                maxLength: maxLength(200) 
            }
        },
    },
    mounted() {
        if (this.$route.params.id) {
            this.buscarObjeto(this.$route.params.id);
        }       
    },
    methods: {

        submit: function(event){

            this.$v.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            let obj = {...this.form};
            
            let request = null;
            if(obj.id)    
                request = userService.atualizarGrupo(obj);
            else
                request = userService.criarGrupo(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    this.form = response.data;
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        buscarObjeto: function(id){
            userService.buscarGrupo(id)
                .then(  response => {
                    this.form = response.data;
                    this.permissoes = response.data.permissions;
                }).catch( erro => this.erros = erro)
        },

        abrirModalDesvincular: function(permissao){
            this.sucessoPermissoes = null;
            this.erroPermissoes = null;
            this.permissaoModal = {...permissao};
        },

        desvincularPermissao: function(){
            this.sucessoPermissoes = null;
            this.errosPermissoes = null;
            userService.desvincularPermissao(this.form.id, this.permissaoModal.id)
            .then( response => {
                this.permissoes = this.permissoes.filter(x => x.id != this.permissaoModal.id);
                this.sucessoPermissoes = 'Permissão desvinculada com sucesso!';
            }).catch( erro => this.errosPermissoes = erro)
        },

         abrirModalVincular: function(){
            this.sucessoPermissoes = null;
            this.erroPermissoes = null;
            this.permissaoModal = null;
            this.buscarPermissoes();
        },

        vincularPermissao: function(){
            this.sucessoPermissoes = null;
            this.errosPermissoes = null;
            userService.vincularPermissao(this.form.id, this.permissaoModal.id)
            .then( response => {
                this.permissoes.push({...response.data}) ;
                this.sucessoPermissoes = `Permissão: ${response.data.name} vinculada com sucesso!`;
            }).catch( erro => this.errosPermissoes = erro)
        },

        buscarPermissoesDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarPermissoes(text, loading)
            }, 700);
        },

        buscarPermissoes: function(text, loading){
            this.permissoesBusca = [];
            this.buscandoPermissao = true;
            userService.listarPermissoes({texto: text, per_page:50, guard_name: this.form.guard_name, grupo_id: this.form.id})
                .then( response => this.permissoesBusca = response.data.data)
                .catch( erro => this.errosPermissoes = erro)
                .finally( () => this.buscandoPermissao = false)
        }

    }
};
</script>
